<template>
  <div class="cxs">
    <!-- 我与榜样初相识 -->
    <button class="btn back el-icon-back animated bounceIn" @click="back"><span></span></button>
    <button class="btn name animated bounceIn">文明榜样：{{this.$route.query.name}}<span></span></button>
    <section class="container animated fadeIn" style="animation-duration: 3000ms;">
      <!-- 动态将轮播图的容器高度设置成与图片一致 -->
      <el-carousel :interval="4000" type="card" class="animated fadeInDown">
        <el-carousel-item v-for="item in img_list" :key="item">
          <!--遍历图片地址 ， 动态生成轮播图-->
          <img :src="item" alt="#" width="100%" height="100%" />
        </el-carousel-item>
      </el-carousel>
    </section>
    <p
      class="animated fadeInUp font_speed"
      v-show="istrue2"
      style="animation-duration: 3000ms"
    >
      {{ this.$route.query.ry1 }}
    </p>
    <p
      class="animated fadeInUp font_speed"
      v-show="istrue3"
      style="animation-duration: 3000ms"
    >
      {{ this.$route.query.sj1 }}
    </p>
    <p
      class="animated fadeInUp font_speed"
      v-show="istrue4"
      style="animation-duration: 3000ms"
    >
      {{ this.$route.query.sj2 }}
    </p>
    <p
      class="animated fadeInUp font_speed"
      v-show="istrue5"
      style="animation-duration: 3000ms"
    >
      {{ this.$route.query.sj3 }}
    </p>

    <!-- <img :src="img_src" alt="#" /> -->
    <!-- <img src="../assets/wxm/wxm1.jpg" alt="$" /> -->
    <!-- <img :src="img_src" alt="#"> -->
  </div>
</template>

<script>
    export default {
        name: "Cxs",

        data() {
            return {


                // 图片地址
                img_list: [
                    require('../assets/wxf/wxf1.jpg'),
                    require('../assets/wxf/wxf2.jpg'),
                    require('../assets/wxf/wxf3.jpg'),

                    // this.$route.query.img_One,
                    // this.$route.query.img_Two,
                    // this.$route.query.img_Three,
                ],
                // 图片父容器高度
                bannerHeight: 300,
                // 浏览器高度
                screenWidth: 0,

                istrue1: false,
                istrue2: false,
                istrue3: false,
                istrue4: false,
                istrue5: false,
            };
        },

        methods: {
            setSize: function() {
                // 通过浏览器宽度计算高度
                this.bannerHeight = (400 / 1920) * this.screenWidth;
                // console.log(this.bannerHeight);
            },

            back() {
                this.$router.back(-1);
            }
        },

        mounted() {
            sessionStorage.setItem(this.$route.query.name,true);
            console.log(sessionStorage);
            sessionStorage.setItem('one', true);
            console.log(sessionStorage.getItem('one'))
            // 首次加载调用一次
            this.screenWidth = window.innerWidth;
            this.setSize();
            // 窗口大小发生改变，调用一次
            window.onresize = () => {
                this.screenWidth = window.innerWidth;
                this.setSize();
            };

            // this.img_src = require('../assets/' + this.$route.query.img_Three);
            // this.img_list = require('../assets/' + this.$route.query.img_One + '.jpg');
            this.img_list = [
                require('../assets/' + this.$route.query.img_One),
                require('../assets/' + this.$route.query.img_Two),
                require('../assets/' + this.$route.query.img_Three),

                // this.$route.query.img_One,
                // this.$route.query.img_Two,
                // this.$route.query.img_Three,
            ];

        },

        created() {
            setTimeout(() => {
                this.istrue1 = true;
            }, 1000);
            setTimeout(() => {
                this.istrue2 = true;
            }, 2500);
            setTimeout(() => {
                this.istrue3 = true;
            }, 4000);
            setTimeout(() => {
                this.istrue4 = true;
            }, 5500);
            setTimeout(() => {
                this.istrue5 = true;
            }, 7000);
        },
    };
</script>

<style scoped>
    /deep/.el-carousel--horizontal {
        overflow-x: visible;
    }
    
    .el-carousel__item {
        border: 0.5rem solid white;
    }
    
    .container {
        width: 70%;
        margin-left: 15%;
        margin-top: 8rem;
    }
    
    .container img {
        width: 100%;
        vertical-align: middle;
    }
    
    .btn {
        position: relative;
        padding: 0.8rem;
        margin-top: 0.8rem;
        margin-bottom: 0.8rem;
        border-radius: 10rem;
        border: none;
        background: linear-gradient(-45deg, #215eb1, rgb(91, 159, 248));
        color: #fff;
        cursor: pointer;
        outline: none;
        border: 2px solid #eee;
        overflow: hidden;
    }
    
    .btn span {
        display: block;
        width: 10rem;
        height: 0.4rem;
        background: rgba(255, 255, 255, 0.596);
        opacity: .5;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateX(-100%) translateY(50%) rotate(45deg);
        transition: all 1.5s ease;
    }
    
    .back {
        position: fixed;
        left: 2rem;
        top: calc(50% - 1rem);
        z-index: 9;
        font-weight: bolder;
        font-size: 2rem;
    }
    
    .name {
        position: absolute;
        font-size: 1.5rem;
        padding: 1rem;
        left: calc(50% - 7rem);
        top: 1.5rem;
        border: 0.2rem solid white;
    }
    
    .name:hover span {
        left: 30rem;
    }
    
    .cxs {
        width: 100vw;
        height: 100vh;
        background-image: url('../assets/main-bg.jpg');
        overflow: hidden;
    }
    
    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 200px;
        margin: 0;
    }
    
    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }
    
    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
    
    p {
        font-size: 1.1rem;
    }
    
    .back_icon {
        float: left;
        margin-left: 2rem;
        width: 3rem;
        height: 3rem;
        cursor: pointer;
    }
</style>


