                setTimeout(() => {
                    this.IsShow = false;
                    this.zs = true;
                    this.qc = true;
                }, 1300);
            },

            zs_next() {
                this.item = true;
                setTimeout(() => {
                    this.qc = false;
                    this.jnp = true;
                }, 1000);
            },

            jnp_next() {
                this.jnp_item = true;
                setTimeout(() => {
                    this.jnp = false;
                    this.cp = true;
                }, 1000);
            },

            cp_next() {
                this.cp_item = true;
                setTimeout(() => {
                    this.cp = false;
                    this.$router.push({
                        path: "/"
                    });
                }, 1000);
            },
