<template>
  <div class="home">
    <h1 class="animated bounceInRight"><span>我<br>与<br>榜<br>样</span><span style="display: block;">初<br>相<br>识</span></h1>
    <img src="../assets/logo.png" alt="" class="logo">
    <button class="btn back el-icon-back animated bounceIn" @click="back"><span></span></button>
    <section class="animated fadeIn map_box">
    <div id="mapChart" class="map animated pulse container" ref="mapChart"></div>
    </section>
  </div>
</template>

<script>
    import data from "../../static/js/data";
    import Vue from 'vue'
    export default {
        name: "home",
        data() {
            return {
                data: null,
                index: 0,
            }
        },
        mounted() {
            this.mapFn();
            this.$notify.info({
                title: '提示',
                message: '点击对应子地图即可获取当地的典型榜样。',
                position: "top-left",
                duration: 5000
            });
        },
        created() {
            this.data = data;
        },
        methods: {

            back() {
                this.$router.push({
                    path: "/"
                });
            },

            mapFn() {
                // 基于准备好的dom，初始化echarts实例
                // var mapChart = this.$echarts.init(this.$refs.mapChart);
                var mapChart = this.$echarts.init(document.getElementById("mapChart"));
                var geoCoordMap = {
                    常德市: [111.691347, 29.200225],
                    郴州市: [113.032067, 25.793589],
                    衡阳市: [112.607693, 26.900358],
                    怀化市: [109.97824, 27.570082],
                    娄底市: [111.608497, 27.828136],
                    邵阳市: [111.00923, 27.037842],
                    湘潭市: [112.644052, 27.72973],
                    湘西土家族苗族自治州: [109.739735, 28.714296],
                    益阳市: [112.005042, 28.550066],
                    永州市: [111.808019, 26.034516],
                    岳阳市: [113.332855, 29.25029],
                    长沙市: [112.982279, 28.19409],
                    株洲市: [113.551737, 27.235806],
                    张家界市: [110.479921, 29.607401],
                };
                window.dataList = [{
                    name: "常德市",
                    value: "王新法"
                }, {
                    name: "郴州市",
                    value: "首嫣嫣"
                }, {
                    name: "衡阳市",
                    value: "向文韬"
                }, {
                    name: "怀化市",
                    value: "罗小小"
                }, {
                    name: "娄底市",
                    value: "肖光盛"
                }, {
                    name: "邵阳市",
                    value: "杨淑亭"
                }, {
                    name: "湘潭市",
                    value: "许月华"
                }, {
                    name: "湘西土家族苗族自治州",
                    value: "田全友"
                }, {
                    name: "益阳市",
                    value: "吴先明"
                }, {
                    name: "永州市",
                    value: "潘晓红"
                }, {
                    name: "岳阳市",
                    value: "彭庆文"
                }, {
                    name: "长沙市",
                    value: "任菲莉"
                }, {
                    name: "株洲市",
                    value: "夏昭炎"
                }, {
                    name: "张家界市",
                    value: "周海燕"
                }, ];

                var convertData = function(data) {
                    var res = [];
                    for (var i = 0; i < data.length; i++) {
                        var geoCoord = geoCoordMap[data[i].name];
                        if (geoCoord) {
                            res.push({
                                name: data[i].name,
                                value: geoCoord.concat(data[i].value),
                            });
                        }
                    }
                    return res;
                };
                mapChart.setOption({
                    legend: {
                        orient: 'vertical',
                        y: 'bottom',
                        x: 'right',
                        data: ['pm2.5'],
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    visualMap: {
                        show: false,
                        min: 0,
                        max: 500,
                        left: 'left',
                        top: 'bottom',
                        text: ['高', '低'], // 文本，默认为数值文本
                        calculable: true,
                        seriesIndex: [1],
                        inRange: {

                        }
                    },
                    geo: {
                        map: "湖南",
                        center: [111.00923, 27.037842],
                        roam: true,
                        zoom: 1, //当前视角缩放比例
                        label: {
                            normal: {
                                show: false
                            },
                            emphasis: {
                                show: false,
                            }
                        },
                        itemStyle: {
                            normal: {
                                areaColor: '#3a7fd5',
                                borderColor: '#0a53e9', //线
                                shadowColor: '#092f8f', //外发光
                                shadowBlur: 20
                            },
                            emphasis: {
                                areaColor: '#0a2dae', //悬浮区背景
                            }
                        }
                    },
                    series: [{
                        symbolSize: 5,
                        label: {
                            normal: {
                                formatter: '{b}',
                                position: 'right',
                                show: true
                            },
                            emphasis: {
                                show: true
                            }
                        },
                        itemStyle: {
                            normal: {
                                color: '#fff'
                            }
                        },
                        name: 'light',
                        type: 'scatter',
                        coordinateSystem: 'geo',
                        data: convertData(dataList),

                    }, , { //鼠标hover的效果
                        type: 'map',
                        map: '湖南',
                        geoIndex: 0,
                        aspectScale: 0.75, //长宽比
                        showLegendSymbol: false, // 存在legend时显示
                        label: {
                            normal: {
                                show: false
                            },
                            emphasis: {
                                show: false,
                                textStyle: {
                                    color: '#fff'
                                }
                            }
                        },
                        roam: true,
                        itemStyle: {
                            normal: {
                                areaColor: '#031525',
                                borderColor: '#FFFFFF',
                            },
                            emphasis: {
                                areaColor: '#2B91B7'
                            }
                        },
                        animation: false,
                        data: [{
                            name: '常德市',
                            value: "1"
                        }, {
                            name: '郴州市',
                            value: "2"
                        }, {
                            name: '衡阳市',
                            value: "3"
                        }, {
                            name: '怀化市',
                            value: "4"
                        }, {
                            name: '娄底市',
                            value: "5"
                        }, {
                            name: '邵阳市',
                            value: "6"
                        }, {
                            name: '湘潭市',
                            value: "7"
                        }, {
                            name: '湘西土家族苗族自治州',
                            value: "8"
                        }, {
                            name: '益阳市',
                            value: "9"
                        }, {
                            name: '永州市',
                            value: "10"
                        }, {
                            name: '岳阳市',
                            value: "11"
                        }, {
                            name: '长沙市',
                            value: "12"
                        }, {
                            name: '株洲市',
                            value: "13"
                        }, {
                            name: '张家界市',
                            value: "14"
                        }]
                    }, { //气泡
                        type: "scatter",
                        coordinateSystem: "geo",
                        symbol: "pin",
                        symbolSize: [85, 85],
                        label: {
                            normal: {
                                show: true,
                                textStyle: {
                                    color: "#fff",
                                    fontSize: 15,
                                    fontWeight: 500,
                                },
                                formatter(value) {
                                    return value.data.value[2];
                                },
                            },
                        },
                        hoverAnimation: true,
                        itemStyle: {
                            normal: {
                                color: "#D8BC37", // 标志颜色
                            },
                        },
                        zlevel: 99999,
                        data: convertData(window.dataList),
                    }]
                });
                window.addEventListener("resize", () => {
                    // 自动渲染echarts
                    mapChart.resize();

                });
                // 点击事件
                mapChart.on('click', (params) => {
                    // 跳转路由
