                                    name: this.data.msg.wxf.name,
                                    ry1: this.data.msg.wxf.ry1,
                                    sj1: this.data.msg.wxf.sj1,
                                    sj2: this.data.msg.wxf.sj2,
                                    sj3: this.data.msg.wxf.sj3,
                                    img_One: this.data.msg.wxf.img_One,
