                                    name: this.data.msg.syy.name,
                                    ry1: this.data.msg.syy.ry1,
                                    sj1: this.data.msg.syy.sj1,
                                    sj2: this.data.msg.syy.sj2,
                                    sj3: this.data.msg.syy.sj3,
                                    img_One: this.data.msg.syy.img_One,
                                    img_Two: this.data.msg.syy.img_Two,
                                    img_Three: this.data.msg.syy.img_Three
                                }
                            });
                            break;

                        case 3:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.xwt.name,
                                    ry1: this.data.msg.xwt.ry1,
                                    sj1: this.data.msg.xwt.sj1,
                                    sj2: this.data.msg.xwt.sj2,
                                    sj3: this.data.msg.xwt.sj3,
                                    img_One: this.data.msg.xwt.img_One,
                                    img_Two: this.data.msg.xwt.img_Two,
                                    img_Three: this.data.msg.xwt.img_Three
                                }
                            });
                            break;

                        case 4:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.lxx.name,
                                    ry1: this.data.msg.lxx.ry1,
                                    sj1: this.data.msg.lxx.sj1,
                                    sj2: this.data.msg.lxx.sj2,
                                    sj3: this.data.msg.lxx.sj3,
                                    img_One: this.data.msg.lxx.img_One,
                                    img_Two: this.data.msg.lxx.img_Two,
                                    img_Three: this.data.msg.lxx.img_Three
                                }
                            });
                            break;

                        case 5:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.xgs.name,
                                    ry1: this.data.msg.xgs.ry1,
                                    sj1: this.data.msg.xgs.sj1,
                                    sj2: this.data.msg.xgs.sj2,
                                    sj3: this.data.msg.xgs.sj3,
                                    img_One: this.data.msg.xgs.img_One,
                                    img_Two: this.data.msg.xgs.img_Two,
                                    img_Three: this.data.msg.xgs.img_Three
                                }
                            });
                            break;

                        case 6:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.yst.name,
                                    ry1: this.data.msg.yst.ry1,
                                    sj1: this.data.msg.yst.sj1,
                                    sj2: this.data.msg.yst.sj2,
                                    sj3: this.data.msg.yst.sj3,
                                    img_One: this.data.msg.yst.img_One,
                                    img_Two: this.data.msg.yst.img_Two,
                                    img_Three: this.data.msg.yst.img_Three
                                }
                            });
                            break;

                        case 7:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.xyh.name,
                                    ry1: this.data.msg.xyh.ry1,
                                    sj1: this.data.msg.xyh.sj1,
                                    sj2: this.data.msg.xyh.sj2,
                                    sj3: this.data.msg.xyh.sj3,
                                    img_One: this.data.msg.xyh.img_One,
                                    img_Two: this.data.msg.xyh.img_Two,
                                    img_Three: this.data.msg.xyh.img_Three
                                }
                            });
                            break;

                        case 8:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.tqy.name,
                                    ry1: this.data.msg.tqy.ry1,
                                    sj1: this.data.msg.tqy.sj1,
                                    sj2: this.data.msg.tqy.sj2,
                                    sj3: this.data.msg.tqy.sj3,
                                    img_One: this.data.msg.tqy.img_One,
                                    img_Two: this.data.msg.tqy.img_Two,
                                    img_Three: this.data.msg.tqy.img_Three
                                }
                            });
                            break;

                        case 9:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.wxm.name,
                                    ry1: this.data.msg.wxm.ry1,
                                    sj1: this.data.msg.wxm.sj1,
                                    sj2: this.data.msg.wxm.sj2,
                                    sj3: this.data.msg.wxm.sj3,
                                    img_One: this.data.msg.wxm.img_One,
                                    img_Two: this.data.msg.wxm.img_Two,
                                    img_Three: this.data.msg.wxm.img_Three
                                }
                            });
                            break;

                        case 10:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.pxh.name,
                                    ry1: this.data.msg.pxh.ry1,
                                    sj1: this.data.msg.pxh.sj1,
                                    sj2: this.data.msg.pxh.sj2,
                                    sj3: this.data.msg.pxh.sj3,
                                    img_One: this.data.msg.pxh.img_One,
                                    img_Two: this.data.msg.pxh.img_Two,
                                    img_Three: this.data.msg.pxh.img_Three
                                }
                            });
                            break;

                        case 11:
                            this.$router.push({
                                path: "/cxs",
                                query: {
                                    name: this.data.msg.pqw.name,
                                    ry1: this.data.msg.pqw.ry1,
                                    sj1: this.data.msg.pqw.sj1,
                                    sj2: this.data.msg.pqw.sj2,
                                    sj3: this.data.msg.pqw.sj3,
                                    img_One: this.data.msg.pqw.img_One,
                                    img_Two: this.data.msg.pqw.img_Two,
                                    img_Three: this.data.msg.pqw.img_Three
                                }
