</script>

<style scoped>
    .logo {
        position: absolute;
        right: 1rem;
        bottom: 1rem;
        width: 5rem;
        opacity: .6;
    }
    
    .home {
        position: fixed;
        height: 100%;
        width: 100%;
        background-image: url('../assets/main-bg.jpg');
    }
    
    #mapChart {
        position: fixed;
        top: calc(50% - 23rem);
        left: calc(50% - 25rem);
        width: 46rem;
        height: 46rem;
        animation: mymove 4s infinite;
    }
    
    @keyframes mymove {
        0% {
            transform: translate(0px, 0px);
        }
        50% {
            transform: translate(0px, -10px);
        }
        100% {
            transform: translate(0px, 0px);
        }
    }
    
    .btn {
        position: absolute;
        left: 10rem;
        top: calc(50% - 2rem);
        padding: 0.8rem;
        margin-top: 0.8rem;
        margin-bottom: 0.8rem;
        border-radius: 10rem;
        border: none;
        background: linear-gradient(-45deg, #215eb1, rgb(91, 159, 248));
        color: #fff;
        cursor: pointer;
        outline: none;
        border: 2px solid #eee;
        overflow: hidden;
        font-size: 2rem;
        z-index: 99;
    }
    
    .btn span {
        display: block;
        width: 10rem;
        height: 0.4rem;
        background: rgba(255, 255, 255, 0.596);
        opacity: .5;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateX(-100%) translateY(50%) rotate(45deg);
        transition: all 1.5s ease;
    }
    
    .back:hover span {
        left: 15rem;
    }
    /* 文字特效 */
    
    h1 {
        position: fixed;
        z-index: 99;
        top: calc(50% - 10.5rem);
        right: 3rem;
        margin: 0;
        font-size: 3rem;
        padding: 0;
        font-family: font3;
        color: white;
        text-shadow: 0 0.1em 20px black, 0.05em -0.03em 0 black, 0.05em 0.005em 0 black, 0em 0.08em 0 black, 0.05em 0.08em 0 black, 0px -0.03em 0 black, -0.03em -0.03em 0 black, -0.03em 0.08em 0 black, -0.03em 0 0 black;
    }
    
    h1 span {
        -webkit-transform: scale(0.9);
        transform: scale(0.9);
        display: inline-block;
    }
    
    h1 span:first-child {
        -webkit-animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    h1 span:last-child {
        -webkit-animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    @-webkit-keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @-webkit-keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
    
    @keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
</style>


