                        name: name
                    }
                })
            },
            back() {
                this.$router.back(-1);
            },
            prev() {
                this.$refs.mypic.prev();
            },
            next() {
                this.$refs.mypic.next();
            }
        }
    }
</script>
<style scoped>
    .content .logo {
        position: absolute;
        bottom: 1rem;
        right: 1rem;
        opacity: .4;
        z-index: -1;
        width: 3rem;
    }
    
    .el-carousel__item h3 {
        color: #475669;
        font-size: 0.4rem;
        opacity: 0.75;
        line-height: 5rem;
        margin: 0;
    }
    
    .el-carousel--vertical {
        height: 100%;
    }
    
    .content {
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 80%;
        margin-left: 10%;
        align-items: flex-start;
    }
    
    .content>div {
        position: relative;
        top: 50%;
        text-align: center;
        width: 23%;
        text-align: left;
        transform: translateY(-50%);
    }
    
    .content>div:nth-of-type(2)>div {
        animation-delay: 0.1s !important;
    }
    
    .content>div:nth-of-type(3)>div {
        animation-delay: 0.2s !important;
    }
    
    .content>div:nth-of-type(4)>div {
        animation-delay: 0.3s !important;
    }
    
    .content>div>div {
        border: 0.3rem solid #fff;
        padding: 0.6rem;
        background: rgba(243, 243, 243, 0.411);
        transition-duration: 1.5s;
    }
    
    .content img {
        vertical-align: middle;
    }
    
    .content p img {
        margin-right: 0.2rem;
    }
    
    .name {
        font-weight: 1000;
        margin-top: 1rem;
        margin-bottom: 0.6rem;
    }
    
    .intro {
        font-size: 0.9rem;
        font-weight: 1000;
        margin-top: 0.6rem;
        margin-bottom: 0.6rem;
    }
    
    .honor>p {
        font-size: 0.9rem;
        margin-top: 0.3rem;
        margin-bottom: 0.3rem;
    }
    
    .tx {
        width: 100%;
        height: 11rem;
    }
    
    .qhy {
        height: 100%;
        width: 100%;
        position: fixed;
        background: url(../assets/main-bg.jpg);
    }
    
    .qhy-btn {
        position: relative;
        padding: 0.8rem;
        margin-top: 0.8rem;
        margin-bottom: 0.8rem;
        border-radius: 10rem;
        border: none;
        background: linear-gradient(-45deg, #215eb1, rgb(91, 159, 248));
        color: #fff;
        cursor: pointer;
        outline: none;
        border: 2px solid #eee;
        overflow: hidden;
        left: 50%;
        transform: translateX(-50%);
    }
    
    .qhy-btn span {
        display: block;
        width: 10rem;
        height: 0.4rem;
        background: rgba(255, 255, 255, 0.596);
        opacity: .5;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateX(-100%) translateY(50%) rotate(45deg);
        transition: all 1.5s ease;
    }
    
    .qhy-btn:hover span {
        left: 15rem;
    }
    
    .next,
    .prev {
        position: absolute;
        font-size: 1rem;
        z-index: 99;
        padding: 0.8rem;
        background: rgba(0, 0, 0, 0.068);
        border-radius: 50%;
        color: #fff;
        border: none;
        outline: none;
        text-align: center;
        left: 50%;
        transform: translateX(-50%);
        transition: all 0.4s ease;
        cursor: pointer;
    }
    
    .prev {
        top: 1rem;
        transform: rotate(180deg) translateX(-50%);
        transform-origin: 0% 50%;
    }
    
    .next {
        bottom: 1rem;
    }
    
    .next:hover,
    .prev:hover {
        background: rgba(0, 0, 0, 0.205);
    }
    
    .back {
        position: fixed;
        left: 2rem;
        top: calc(50% - 1rem);
        z-index: 9;
        font-weight: bolder;
        font-size: 2rem;
    }
    
    .back span {
        transition-duration: 2s !important;
    }
    
    .lock {
        position: absolute;
        z-index: 99;
        border: none !important;
        background: rgba(128, 128, 128, 0.945) !important;
        height: 100%;
        width: 100%;
        padding: 0 !important;
        margin: -0.6rem;
    }
    
    .lock img {
        position: relative;
        width: 50%;
        z-index: 99;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
    }
    /* 文字特效 */
    
    h1 {
        position: fixed;
        z-index: 99;
        top: calc(50% - 10.5rem);
        right: 3rem;
        margin: 0;
        font-size: 3rem;
        padding: 0;
        font-family: font3;
        color: white;
        animation-delay: 0.5s;
        text-shadow: 0 0.1em 20px black, 0.05em -0.03em 0 black, 0.05em 0.005em 0 black, 0em 0.08em 0 black, 0.05em 0.08em 0 black, 0px -0.03em 0 black, -0.03em -0.03em 0 black, -0.03em 0.08em 0 black, -0.03em 0 0 black;
    }
    
    h1 span {
        -webkit-transform: scale(0.9);
        transform: scale(0.9);
        display: inline-block;
    }
    
    h1 span:first-child {
        -webkit-animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    h1 span:last-child {
        -webkit-animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    @-webkit-keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @-webkit-keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
    
    @keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
</style>


