<template>
    <div class="hy">
        <button class="btn back el-icon-back animated bounceIn" @click="back"><span></span></button>
        <button class="btn message animated bounceIn" v-if="success">鼠标右键点击相框保存合影图<span></span></button>
        <h1 class="animated bounceInRight"><span>我<br>同<br>榜<br>样</span><span style="display: block;">齐<br>合<br>影</span></h1>
        <p class="ts animated wobble">上传照片待后台处理完毕后即可生成与偶像的合照啦~</p>
        <img ref="conf0" :src="byimgUrl" width="400px" v-show="false">
        <img src="../assets/logo.png" alt="" class="logo">
        <div class="layui-input-inline">
            <div class="layui-upload-list" v-show="false">
                <img class="layui-upload-img" name="titleBase64Img" id="base64Img" src="" style="display: none" width="300px">
            </div>
            <input type="file" id="image" lay-verify="required" @change="toBase64()" accept="image/jpeg,image/png,image/jpg">
            <div class="upload animated fadeInUp">
                <p>{{msg}}</p>
            </div>
        </div>
        <canvas width="640" height="360" style="border:1rem solid white;animation-duration:3s;" id="mycanvas" class="animated fadeIn"></canvas>
        <div v-show="false"><img :src="imgUrl" alt="" ref="dPic"></div>
    </div>
</template>
<script>
    import md5 from 'js-md5';
    export default {
        data() {
            return {
                imgUrl: null,
                byimgUrl: require('../assets/picFrame/' + this.$route.query.name + '.jpg'),
                imgBase64: null,
                ctx: null,
                fileList: [],
                msg: "上传照片",
                success: false
            }
        },
        created() {

        },
        mounted() {
            sessionStorage.setItem(this.$route.query.name + '_szf', true);
            this.$notify.info({
                title: '提示',
                message: '点击上传照片并选择完照片后，后台会自动完成抠图生成合影照。',
                position: "top-left",
                duration: 5000
            });
            var that = this;
            var mycanvas = document.getElementById('mycanvas');
            this.ctx = mycanvas.getContext('2d');
            var img = this.$refs.conf0;
            img.onload = () => {
                this.ctx.drawImage(img, 0, 0, 640, 360);
            };
        },
        methods: {
            toBase64() {
                console.log('触发事件！');
                var that = this;
                $('#image').attr("disabled", true);
                that.msg = "正在上传...";
                var file = document.querySelector('input[type=file]').files[0];
                sessionStorage.setItem('two', true);
                var reader = new FileReader();
                reader.onloadend = function() {
                    console.log(3)
                    $("#base64Img").attr("style", "display:inline-block");
                    $("#base64Img").attr("src", reader.result);
                    that.imgBase64 = reader.result.substring(23);
                    console.log(that.imgBase64);

                    $.ajax({
                        type: "POST",
                        url: "http://perdc.market.alicloudapi.com/bodyseg",
                        dataType: "json",
                        headers: {
                            "Authorization": "APPCODE eff8274606204ce7b52fc0d8680190f1",
                        },
                        data: {
                            "src": that.imgBase64
                        },
                        success: function(data) {
                            console.log(data);
                            console.log("抠图成功！");
                            that.imgUrl = data.msg;
                            let img = that.$refs.dPic;
                            let cr = 640 / 360;
                            img.onload = () => {
                                let imgW = img.width;
                                let imgH = img.height;
                                console.log(imgW, imgH);
                                that.ctx.drawImage(img, 10, 373 - 120 * imgH / imgW * cr, 120 * cr, 120 * imgH / imgW * cr);
                                that.msg = "已完成";
                                that.success = true;
                            }
                        },
                        error: function() {
                            that.msg = "错误，请重试";
                        }

                    });
                    // that.axios.post('/api/bodyseg', {
                    //     data: {
                    //         src: that.imgBase64
                    //     },
                    //     config: {
                    //         headers: {
                    //             "Authorization": "APPCODE eff8274606204ce7b52fc0d8680190f1",
                    //             "X-Ca-Timestamp": new Date().getTime(),
                    //             "gateway_channel": "http",
                    //             "X-Ca-Key": "203905733",
                    //             "x-ca-nonce": "54aa732b-6608-43c6-8a7b-2264bdbb7f00",
                    //             "X-Ca-Request-Mode": "DEBUG",
                    //             "X-Ca-Stage": "RELEASE",
                    //             "X-Ca-Supervisor-Token": "eyJhbGciOiJSUzI1NiJ9.eyJpYXQiOjE2MTIyNTE1MTEsIm5iZiI6MTYxMjI1MTQ1MSwiaXNzIjoiQWxpeXVuQXBpR2F0ZXdheSIsInJvbGUiOiJ1c2VyIiwiYXVkIjoiYXBpLXNoYXJlZC12cGMtMDAxIiwiZXhwIjoxNjEyMjUyNDExLCJqdGkiOiI0NzAwNDA3N2E2YzI0YTNiOGE1NzQ0MDc2ZDNiNTdjNyIsImFjdGlvbiI6IkRFQlVHIiwidWlkIjoiMTk1NjY2NTY5NDQ3NTM4NyJ9.L8XqIywIoO3rAXWYC-J-hpQ5vDwpte67vtpycZQ3yjj_I2nAhC7BUqb9W9Hw8dpzEOjGRB8MfxOezA7i82uqICOyZKnHE2imaEr38sSeU-P-MOLbkiMqMJfaY-34wKDEOGOdM7W1Zh3ALBye6bjkPegGBkdCyUH33fhNLPRfGfXQH6FjZsScwvcZKCn4QdbNn8ki784JxLgcDoxbcRcHlcEbvLEEm8MC_8Bkb8I7W_8MWpD-DHUgTMOSyftwFNaCJ0PVZi70rVGvO3JVAkvnK-9dA4Tx2SyH1ZhUTrYvuMc0fJgVOCoNBAg2_sts-nRI88JAzwvkt0xDGbN65JAWjw",
                    //             "Host": "perdc.market.alicloudapi.com",
                    //             "Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
                    //         }
                    //     }
                    // }).then(function(res) {
                    //     console.log("抠图成功！");
                    //     console.log(res)
                    //     that.imgUrl = res.data.result.result.cutout_image_url;
                    //     let img = that.$refs.dPic;
                    //     let cr = 640 / 360;
                    //     img.onload = () => {
                    //         let imgW = img.width;
                    //         let imgH = img.height;
                    //         console.log(imgW, imgH);
                    //         that.ctx.drawImage(img, 10, 373 - 120 * imgH / imgW * cr, 120 * cr, 120 * imgH / imgW * cr);
                    //         that.msg = "已完成";
                    //         that.success = true;
                    //     }
                    // }).catch(function(error) {
                    //     console.log(error);
                    //     that.msg = "错误，请重试";
                    // });
                }
                if (file) {
                    reader.readAsDataURL(file);
                }
            },
            //生成数字签名
            getSign() {
                return md5("ac9d0afee7a3e33da666cececfd41b4b" + new Date().getTime());
            },
            back() {
                this.$router.back(-1);
            }
        }
    }
</script>

<style scoped>
    .ts {
        width: 10rem;
        font-size: 1.5rem;
        background: rgba(255, 255, 255, 0.555);
        padding: 2rem;
        border-radius: 10rem;
        position: absolute;
        bottom: 2rem;
        left: 4rem;
    }
    
    .logo {
        position: absolute;
        right: 1rem;
        bottom: 1rem;
        width: 5rem;
        opacity: .6;
    }
    
    #mycanvas {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
    }
    
    .hy {
        position: fixed;
        height: 100%;
        width: 100%;
        background: url(../assets/main-bg.jpg);
    }
    
    .btn {
        position: relative;
        padding: 0.8rem;
        margin-top: 0.8rem;
        margin-bottom: 0.8rem;
        border-radius: 10rem;
        border: none;
        background: linear-gradient(-45deg, #215eb1, rgb(91, 159, 248));
        color: #fff;
        cursor: pointer;
        outline: none;
        border: 2px solid #eee;
        overflow: hidden;
    }
    
    .btn span {
        display: block;
        width: 10rem;
        height: 0.4rem;
        background: rgba(255, 255, 255, 0.596);
        opacity: .5;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateX(-100%) translateY(50%) rotate(45deg);
        transition: all 1.5s ease;
    }
    
    .message:hover span {
        left: 30rem;
    }
    
    .message {
        font-size: 1.2rem;
        padding: 1.2rem;
        top: 1rem;
        border: 0.2rem solid white;
    }
    
    .back {
        position: fixed;
        left: 2rem;
        top: calc(50% - 1rem);
        z-index: 9;
        font-weight: bolder;
        font-size: 2rem;
    }
    
    #image,
    .upload {
        position: absolute;
        bottom: 2rem;
        left: calc(50% - 4rem);
        height: 2.5rem !important;
        width: 8rem !important;
        border: 1px solid red;
        padding: 0.5rem;
        cursor: pointer;
    }
    
    .upload {
        pointer-events: none;
        border-radius: 10rem;
        background: linear-gradient(-45deg, #215eb1, rgb(91, 159, 248));
        color: #fff;
        cursor: pointer;
        outline: none;
        border: 2px solid #eee;
        overflow: hidden;
    }
    
    .upload p {
        position: absolute;
        margin: 0;
        left: 50%;
        top: 50%;
        width: 100%;
        transform: translateX(-50%) translateY(-50%);
        cursor: pointer;
        font-weight: 1000;
        font-family: font3;
        font-size: 1.2rem;
    }
    
    #image {
        opacity: 0;
    }
    /* 文字特效 */
    
    h1 {
        position: fixed;
        z-index: 99;
        top: calc(50% - 10.5rem);
        right: 3rem;
        margin: 0;
        font-size: 3rem;
        padding: 0;
        font-family: font3;
        color: white;
        text-shadow: 0 0.1em 20px black, 0.05em -0.03em 0 black, 0.05em 0.005em 0 black, 0em 0.08em 0 black, 0.05em 0.08em 0 black, 0px -0.03em 0 black, -0.03em -0.03em 0 black, -0.03em 0.08em 0 black, -0.03em 0 0 black;
    }
    
    h1 span {
        -webkit-transform: scale(0.9);
        transform: scale(0.9);
        display: inline-block;
    }
    
    h1 span:first-child {
        -webkit-animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    h1 span:last-child {
        -webkit-animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    @-webkit-keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @-webkit-keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
    
    @keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
</style>


