<template>
    <div>
        <h1 class="animated bounceInLeft"><span>榜<br>样</span><span style="display: block;">与<br>我</span></h1>
        <img src="../assets/bg.jpg" alt="" class="bg" width="100%" height="100%">
        <img src="../assets/logo.png" alt="" width="100px" class="logo">
        <div class="one move" v-if="one"><img src="../assets/index-one.png" style="animation-duration: 1.2s;"></div>
        <div class="one" v-else><img src="../assets/one-lock.png" alt=""></div>
        <div class="two move" v-if="two"><img src="../assets/index-two.png" style="animation-duration: 1.4s;"></div>
        <div class="two" v-else><img src="../assets/two-lock.png" alt=""></div>
        <div class="three"><img src="../assets/index-three.png"  style="animation-duration: 1.6s;"></div>
        <div class="four"><img src="../assets/index-four.png"  style="animation-duration: 1.8s;"></div>
        <div>
            <el-popover
                placement="top-start"
                title="我同榜样齐合影"
                width="200"
                trigger="hover"
                content="在这里您可以与您心仪的榜样完成合影。">
                <el-button slot="reference" class="one-btn" @click="push('/qhy')">hover 激活</el-button>
            </el-popover>
            <el-popover
                placement="top-start"
                title="我给榜样送祝福"
                width="200"
                trigger="hover"
                content="在这里您可以给你心仪的榜样送上祝福。">
                <el-button slot="reference" class="two-btn" @click="push('/szf')">hover 激活</el-button>
            </el-popover>
            <el-popover
                placement="top-start"
                title="我与榜样初相识"
                width="200"
                trigger="hover"
                content="在这里您可以全面的了解我省各市榜样的详细事迹。">
                <el-button slot="reference" class="three-btn" @click="push('/cxsHome')">hover 激活</el-button>
            </el-popover>
            <el-popover
                placement="top-start"
                title="抗疫事迹博物馆"
                width="200"
                trigger="hover"
                content="在这里您可以了解到我省榜样的抗疫事迹。">
                <el-button slot="reference" class="four-btn" @click="push('/bwg')">hover 激活</el-button>
            </el-popover>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                one: sessionStorage.getItem('one'),
                two: sessionStorage.getItem('two')
            }
        },
        methods: {
            push(url) {
                if (url == '/qhy' && !this.one) {
                    this.$notify.error({
                        title: '未解锁',
                        message: '请先解锁初相识板块对应内容！'
                    });
                    return;
                }
                if (url == '/szf' && !this.two) {
                    this.$notify.error({
                        title: '未解锁',
                        message: '请先解锁齐合影板块对应内容！'
                    });
                    return;
                }
                this.$router.push(url);
            },
        },
        mounted() {
            this.$notify.info({
                title: '欢迎来到我与榜样主题网站',
                message: '您需浏览榜样的信息才可解锁后续相关板块的内容。',
                duration: 5000
            });
        }
    }
</script>
<style scoped>
    .logo {
        position: absolute;
        left: 1rem;
        top: 1rem;
        opacity: .6;
    }
    
    .bg {
        position: fixed;
        left: 0;
        top: 0;
        z-index: -1;
    }
    
    .three img {
        position: fixed;
        width: 18rem;
        left: 15rem;
        top: -1rem;
        animation: move 1.4s linear infinite;
    }
    
    .move img {
        position: fixed;
        width: 18rem;
        left: 15rem;
        top: -1rem;
        animation: move 1.4s linear infinite;
    }
    
    .one img {
        position: fixed;
        width: 22rem;
        left: 25rem;
        top: 17rem;
        /* animation: move 1.2s linear infinite; */
    }
    
    .two img {
        position: fixed;
        width: 20rem;
        left: 55rem;
        top: 8rem;
        /* animation: move 1.3s linear infinite; */
    }
    
    .four img {
        position: fixed;
        width: 20rem;
        bottom: 0rem;
        right: 0rem;
        animation: move 1.4s linear infinite;
    }
    
    .one-btn {
        position: fixed;
        width: 16rem;
        height: 12rem;
        left: 27rem;
        top: 20rem;
        opacity: 0;
    }
    
    .two-btn {
        position: fixed;
        width: 18rem;
        height: 12rem;
        left: 55rem;
        top: 12rem;
        opacity: 0;
    }
    
    .three-btn {
        position: fixed;
        width: 18rem;
        height: 12rem;
        left: 15rem;
        top: 2rem;
        opacity: 0;
    }
    
    .four-btn {
        position: fixed;
        width: 15rem;
        height: 13rem;
        bottom: 2rem;
        right: 2rem;
        opacity: 0;
    }
    /* 文字特效 */
    
    h1 {
        position: fixed;
        z-index: 99;
        left: 3rem;
        top: calc(50% - 8rem);
        margin: 0;
        font-size: 4rem;
        padding: 0;
        font-family: font3;
        color: white;
        text-shadow: 0 0.1em 20px black, 0.05em -0.03em 0 black, 0.05em 0.005em 0 black, 0em 0.08em 0 black, 0.05em 0.08em 0 black, 0px -0.03em 0 black, -0.03em -0.03em 0 black, -0.03em 0.08em 0 black, -0.03em 0 0 black;
    }
    
    h1 span {
        -webkit-transform: scale(0.9);
        transform: scale(0.9);
        display: inline-block;
    }
    
    h1 span:first-child {
        -webkit-animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    h1 span:last-child {
        -webkit-animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    @-webkit-keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @-webkit-keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
    
    @keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
    
    @keyframes move {
        0% {
            transform: translateY(0);
        }
        30% {
            transform: translateY(-8px);
        }
        100% {
            transform: translateY(0);
        }
    }
</style>


