import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Qhy from '@/components/qhy'
import Pt from '@/components/pingtu'
import Hy from '@/components/heying'
import Szf from '@/components/szf'
import Ly from '@/components/ly'
import Home from '@/components/home'
import Cxs from '@/components/cxs'
import CxsHome from '@/components/cxsHome'
import Kybwg from '@/components/kybwg'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Home',
        component: Home
    }, {
        path: '/qhy',
        name: 'Qhy',
        component: Qhy
    }, {
        path: '/qhy-pt',
        name: 'Pt',
        component: Pt
    }, {
        path: '/qhy-hy',
        name: 'Hy',
        component: Hy
    }, {
        path: '/szf',
        name: 'Szf',
        component: Szf
    }, {
        path: '/szf-ly',
        name: 'Ly',
        component: Ly
    }, {
        path: '/cxsHome',
        name: 'CxsHome',
        component: CxsHome
    }, {
        path: '/cxs',
        name: 'Cxs',
        component: Cxs
    }, {
        path: '/bwg',
        name: 'Kybwg',
        component: Kybwg
    }]
})


