<template>
    <div class="szf">
        <h1 class="animated bounceInRight"><span>我<br>给<br>榜<br>样</span><span style="display: block;">送<br>祝<br>福</span></h1>
        <div  class="szf-bg animated fadeIn">
            <img src="../assets/mxp.jpg" alt="" width="100%" height="100%">
            <img :src="byimgUrl" alt="" class="tx animated bounceIn" v-show="tx">
            <img src="../assets/logo.png" alt="" class="mxplogo animated fadeIn" v-if="tx">
            <div class="show1" v-show="tx">
                <p class="pha animated fadeInDown"></p>
                <p class="pha animated fadeInDown"></p>
                <p class="pha animated fadeInDown"></p>
                <p class="pha animated fadeInDown"></p>
            </div>
            <div class="show2" v-show="tx">
                <p class="pha animated fadeInUp"></p>
                <p class="pha animated fadeInUp"></p>
                <p class="pha animated fadeInUp"></p>
                <p class="pha animated fadeInUp"></p>
            </div>
        </div>
        <img src="../assets/logo.png" alt="" class="logo">
        <button class="btn back el-icon-back animated bounceIn" @click="back"><span></span></button>
        <el-form class="input">
            <el-form-item label="请输入对榜样的祝福语（使用空格分隔语句）">
                <el-input type="textarea" v-model="content"></el-input>
            </el-form-item>
        </el-form>
        <button class="btn szf-btn" @click="send" v-if="!tx">送祝福<span></span></button>
        <button class="btn backhome animated bounceIn" @click="backhome" v-else>返回主页<span></span></button>
        <div class="dm">
            <p class="sentence animated fadeInLeft"><span  style="transition-duration: 11s;"><img src="../assets/dz.png" alt="" width="20px" style="vertical-align: bottom;"> <span class="dmc"></span></span></p>
            <p class="sentence animated fadeInLeft"><span  style="transition-duration: 12s;"><img src="../assets/dz.png" alt="" width="20px" style="vertical-align: bottom;"> <span class="dmc"></span></span></p>
            <p class="sentence animated fadeInLeft"><span  style="transition-duration: 13s;"><img src="../assets/dz.png" alt="" width="20px" style="vertical-align: bottom;"> <span class="dmc"></span></span></p>
            <p class="sentence animated fadeInLeft"><span  style="transition-duration: 14s;"><img src="../assets/dz.png" alt="" width="20px" style="vertical-align: bottom;"> <span class="dmc"></span></span></p>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                content: '',
                tx: false,
                byimgUrl: require('../assets/pingtu/' + this.$route.query.name + '.jpg')
            }
        },
        mounted() {
            console.log(this.$route.query.name)
        },
        methods: {
            backhome() {
                this.$router.push('/');
            },
            back() {
                this.$router.back(-1);
            },
            send() {
                this.tx = true;
                var sentence = this.content.split(' ');
                console.log(sentence);
                $('.pha').each(function(i, val) {
                    $(this).html(sentence[i]);
                });
                $('.dmc').each(function(i, val) {
                    $(this).html(sentence[i]);
                });
                this.content = '';
                $('.sentence>span').css('margin-left', "50rem");
            }
        }
    }
</script>
<style scoped>
    /deep/.el-textarea__inner {
        height: 10rem;
    }
    
    .tx {
        position: absolute;
        right: 1rem;
        top: 1rem;
        width: 12rem;
        border-radius: 100%;
        border: 0.2rem solid white;
    }
    
    .mxplogo {
        position: absolute;
        left: 1rem;
        bottom: 1rem;
        width: 6rem;
        opacity: .6;
    }
    
    .show1 {
        position: absolute;
        top: 5rem;
        left: 2rem;
    }
    
    .show1 p {
        text-align: left;
    }
    
    .show2 p {
        text-align: right;
    }
    
    .show2 {
        position: absolute;
        bottom: 2rem;
        right: 2rem;
    }
    
    .dm {
        position: fixed;
        left: 0rem;
        height: 15rem;
        width: 100%;
        background: rgba(255, 255, 255, 0.473);
        transform: rotate(90deg) translateY(calc(-100% - 8rem));
        transform-origin: 0% 0%;
    }
    
    .sentence {
        text-align: left;
        margin-top: 2rem;
        animation-duration: 2s !important;
    }
    
    .sentence>span {
        text-align: left;
        padding: 0.5rem;
        background: #4688df63;
        border-radius: 10rem;
        transition: all 10s linear;
    }
    
    .btn {
        position: relative;
        padding: 0.8rem;
        margin-top: 0.8rem;
        margin-bottom: 0.8rem;
        border-radius: 10rem;
        border: none;
        background: linear-gradient(-45deg, #215eb1, rgb(91, 159, 248));
        color: #fff;
        cursor: pointer;
        outline: none;
        border: 2px solid #eee;
        overflow: hidden;
    }
    
    .btn span {
        display: block;
        width: 10rem;
        height: 0.4rem;
        background: rgba(255, 255, 255, 0.596);
        opacity: .5;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateX(-100%) translateY(50%) rotate(45deg);
        transition: all 1.5s ease;
    }
    
    .back {
        position: absolute;
        left: 2rem;
        font-size: 2rem;
        top: calc(50% - 2rem);
    }
    
    .szf-btn {
        position: absolute;
        bottom: 1rem;
        font-size: 1rem;
        left: calc(50% - 2rem);
        border: 0.1rem solid white;
    }
    
    .backhome {
        position: absolute;
        bottom: 1rem;
        font-size: 1rem;
        left: calc(50% - 2.5rem);
        border: 0.1rem solid white;
    }
    
    .szf-btn:hover span {
        left: 15rem;
    }
    
    .szf {
        position: fixed;
        height: 100%;
        width: 100%;
        background: url(../assets/main-bg.jpg);
    }
    
    .logo {
        position: absolute;
        right: 1rem;
        bottom: 1rem;
        width: 5rem;
        opacity: .6;
    }
    
    .szf-bg {
        position: fixed;
        width: 40rem;
        height: 25rem;
        top: 2rem;
        left: calc(50% - 20rem);
        border: 0.5rem solid white;
    }
    
    .input {
        width: 40%;
        margin-left: 30%;
        position: absolute;
        bottom: 5rem;
    }
    /* 文字特效 */
    
    h1 {
        position: fixed;
        z-index: 99;
        top: calc(50% - 10.5rem);
        right: 3rem;
        margin: 0;
        font-size: 3rem;
        padding: 0;
        font-family: font3;
        color: white;
        text-shadow: 0 0.1em 20px black, 0.05em -0.03em 0 black, 0.05em 0.005em 0 black, 0em 0.08em 0 black, 0.05em 0.08em 0 black, 0px -0.03em 0 black, -0.03em -0.03em 0 black, -0.03em 0.08em 0 black, -0.03em 0 0 black;
    }
    
    h1 span {
        -webkit-transform: scale(0.9);
        transform: scale(0.9);
        display: inline-block;
    }
    
    h1 span:first-child {
        -webkit-animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    h1 span:last-child {
        -webkit-animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
        animation: bopB 1s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
    }
    
    @-webkit-keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @keyframes bop {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        50%,
        100% {
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    @-webkit-keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
    
    @keyframes bopB {
        0% {
            -webkit-transform: scale(0.9);
            transform: scale(0.9);
        }
        80%,
        100% {
            -webkit-transform: scale(1) rotateZ(-3deg);
            transform: scale(1) rotateZ(-3deg);
        }
    }
</style>


